body {
  background-color: #282c34;
  outline: none;
}

.App {
  text-align: center;
}

input.input-dl {
  font-size: 20px;
  padding: 10px 20px;
  width: 40%;
  border-radius: 50px 0 0 50px;
  border: none
}

button.btn-dl {
  font-size: 20px;
  padding: 10px;
  margin-top: 20px;
  width: 100px;
  height: 43px;
  background: #22dd44;
  border: 0;
  border-radius: 0 50px 50px 0 ;
}

h3.status-dl {
  font-weight: 100;;
  color: white;
}

input.input-tags {
  font-size: 20px;
  padding: 10px 20px;
  width: 20%;
  border: #282c34 solid 0.5px
}

input.left-top {
  border-radius: 50px 0 0 0;
}
input.left-bottom {
  border-radius: 0 50px 0 0;
}
input.right-top {
  border-radius: 0 0 0 50px;
}
input.right-bottom {
  border-radius: 0 0 50px 0;
}

button.btn-tag {
  font-size: 20px;
  padding: 10px;
  margin-top: 20px;
  width: 100px;
  height: 43px;
  background: #22dd44;
  border: 0;
  border-radius: 50px ;
}